.container {
  max-width: 1400px;
  width: 1400px;
  height: 300px;
  border-radius: 20px;
  background-color: white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  h1 {
    font-size: 50px;
  }
  span {
    font-size: 60px;
  }
  .back {
    display: flex;
    align-items: center;
    gap: 10px;
    button {
      width: 70px;
      height: 30px;
      background-color: black;
      color: white;
      border-radius: 20px;
    }
    button:active {
      scale: 0.94;
    }
    button:hover {
      background-color: gray;
      color: white;
      transition: 0.2s ease-in;
    }
  }
}
